<template>
    <div>
        <div id="wrap">
            <div id="main" class="clearfix">
                <div id="content">
                    <h3></h3>
                    <h4></h4>
                    <div class="interviewContent">
                         <ul>
                            <li><span>:</span>  {{interviewTypeArr[interviewInfo.interviewType]}}</li>
                            <li><span>:</span> {{interviewInfo.positionName}}</li>
                            <li><span>:</span> {{interviewInfo.interviewTime}}</li>
                            <li><span>:</span> {{interviewInfo.interviewAddress}}</li>
                            <li><span>:</span> {{interviewInfo.contractName}}</li>
                            <li><span>:</span> {{interviewInfo.contractPhone}}</li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
        <footer id="footer">
            <div></div>
        </footer>
    </div>
</template>
<script>

var isAccept = '3';

export default {
    name: 'refusesuccess',
    data() {
        return{
            interviewerId: '',
             interviewTypeArr: {
                1: '',
                2: ''
            },
            interviewInfo: {
                contractPhone: 12345678901,
                interviewTime: "2017-09-21 20:17:38",
                markedWordsInfo: "",
                interviewType: 1,
                companyName: "",
                name: "",
                contractName: "",
                positionName: "",
                interviewAddress: "",
                markedWords: "",
            }
        }
    },
 methods: {
     init(){
        var self = this;
            var method = "interviewer/getInterviewInfo",
            param = JSON.stringify({
                interviewerId: this.interviewerId,
                isAccept: isAccept,
                reasons: this.$route.params.reasons
            }),
            successd = function(res){
                console.log(res.data.data.interviewInfo);
                self.interviewInfo = res.data.data.interviewInfo;
            }
            self.$http(method,param,successd);
     },
  },
   beforeMount(){
      this.interviewerId = this.$route.query.interviewerId;
  },
  mounted () {
      this.init()
  }
}
</script>
<style scoped>
#content h3{
    margin: 0.4rem;
}
h4{
    font-size: 14px;
    text-align: center;
}
.interviewContent{
    width: 90%;
    background-color: #fff;
    font-size: 14px;
    height: 3.4rem;
    margin: 1.2rem auto;
    padding: 0.6rem 0 0 0.3rem;
    line-height: 0.5rem;
}
footer{
    height:18px; 
    font-size:14px;
    color:rgba(102,102,102,1);
    line-height:18px;
    position: fixed;
    bottom: 20px;
    display: inline-block;
    width: 100%;
    text-align: center;  
}
</style>





